@import "../var";
@import "../mixins/common";
@import "../mixins/button";

$btn-prefix: #{$prefix}btn;

.#{$btn-prefix} {
    position: relative;
    box-sizing: border-box;
    border: 1px solid;
    outline: none;
    border-radius: $btn-border-radius;
    line-height: 1;
    margin: 0 7px 12px 0;
    font-weight: $font-weight;
    letter-spacing: 0;
    cursor: pointer;
    transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    font-family: $font-family;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    @mixin loader_size($size) {
        height: $size;
        width: $size;
    }

    &__loader {
        margin-right: 5px;
        @include loader();
    }

    //ripple effect
    &_ripple:after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
        background: radial-gradient(circle, #000 10%, transparent 10.01%) no-repeat 50%;
        transform: scale(10);
        opacity: 0;
        transition: transform 0.5s, opacity 1s;
    }

    &_ripple:active:after {
        transform: scale(0);
        opacity: 0.2;
        transition: 0s;
    }

    &_default {
        @include btn-color($default-color, true);
        border-color: #ccc;
    }

    &_default_outline {
        @include btn-color($default-color, true);
    }

    &_default_link {
        @include btn-text-color($default-color);
    }

    &_default_disabled {
        @include btn-color-disabled($default-color);
    }

    &_primary {
        @include btn-color($primary-color);
    }

    &_primary_outline {
        @include btn-color($primary-color, true);
    }

    &_primary_link {
        @include btn-text-color($primary-color);
    }

    &_primary_disabled {
        @include btn-color-disabled($primary-color);
    }

    &_blue {
        @include btn-color($blue-color);
    }

    &_blue_outline {
        @include btn-color($blue-color, true);
    }

    &_blue_link {
        @include btn-text-color($blue-color);
    }

    &_blue_disabled {
        @include btn-color-disabled($blue-color);
    }

    &_green {
        @include btn-color($green-color);
    }

    &_green_outline {
        @include btn-color($green-color, true);
    }

    &_green_link {
        @include btn-text-color($green-color);
    }

    &_green_disabled {
        @include btn-color-disabled($green-color);
    }

    &_yellow {
        @include btn-color($yellow-color);
    }

    &_yellow_outline {
        @include btn-color($yellow-color, true);
    }

    &_yellow_link {
        @include btn-text-color($yellow-color);
    }

    &_yellow_disabled {
        @include btn-color-disabled($yellow-color);
    }

    &_red {
        @include btn-color($red-color);
    }

    &_red_outline {
        @include btn-color($red-color, true);
    }

    &_red_link {
        @include btn-text-color($red-color);
    }

    &_red_disabled {
        @include btn-color-disabled($red-color);
    }

    &_pink {
        @include btn-color($pink-color);
    }

    &_pink_outline {
        @include btn-color($pink-color, true);
    }

    &_pink_link {
        @include btn-text-color($pink-color);
    }

    &_pink_disabled {
        @include btn-color-disabled($pink-color);
    }

    &_lg {
        font-size: $btn-lg-font-size;
        padding: $btn-lg-padding;

        .#{$btn-prefix}__loader {
            @include loader_size(15px);
        }
    }

    &_md {
        font-size: $btn-md-font-size;
        padding: $btn-md-padding;

        .#{$btn-prefix}__loader {
            @include loader_size(13px);
        }
    }

    &_sm {
        font-size: $btn-sm-font-size;
        padding: $btn-sm-padding;

        .#{$btn-prefix}__loader {
            @include loader_size(11px);
        }
    }

    &_xs {
        font-size: $btn-xs-font-size;
        padding: $btn-xs-padding;

        .#{$btn-prefix}__loader {
            @include loader_size(9px);
        }
    }

    &_block {
        width: 100%;
    }

    &_round {
        border-radius: 30px;
    }

    &_dash {
        border-width: 1px;
        border-style: dashed;
    }

    &-group {
        display: inline-block;
        vertical-align: middle;
        margin-right: 7px;

        .#{$btn-prefix} + .#{$btn-prefix} {
            margin-left: -1px;
        }

        .#{$btn-prefix} {
            border-radius: 0;
            float: left;
            margin: 0;

            &:hover {
                z-index: 1;
            }

            &:first-child {
                border-top-left-radius: $btn-border-radius;
                border-bottom-left-radius: $btn-border-radius;
            }

            &:last-child {
                border-top-right-radius: $btn-border-radius;
                border-bottom-right-radius: $btn-border-radius;
            }
        }

        &_round {
            .#{$btn-prefix} {
                &:first-child {
                    border-top-left-radius: 30px;
                    border-bottom-left-radius: 30px;
                }

                &:last-child {
                    border-top-right-radius: 30px;
                    border-bottom-right-radius: 30px;
                }
            }
        }
    }
}
